import React, { useEffect } from 'react'
import Navbar from './Navbar'
import Footer from './Footer'
import img1 from './../../img/quiznow-4.png';
import img2 from './../../img/quiznow-1.PNG';
import img3 from './../../img/quiznow-2.PNG';
import img4 from './../../img/quiznow-3.PNG';
import { Helmet } from "react-helmet";

const QuizNow = () => {
    useEffect(() => {
        window.scrollTo(0, 0)
    }, [])
    return (
        <div className="container-portfolio">
            <Helmet>
                <meta charSet="utf-8" />
                <title>QuizNow</title>
            </Helmet>
            <Navbar />
            <div className="pages">
                <div className="container">
                    <div className="project-info">
                        <h1>QuizNow</h1>
                        <div className="buttons">
                            <a
                                href="http://quiznow.epizy.com/?i=1"
                                className="button-preview">Live Preview</a>
                            <a
                                href="https://github.com/farukobradovic/QuizNow"
                                className="button-github">Github</a>
                        </div>
                        <div className="desc">
                            QuizNow is created in HTML, CSS, SASS and JS. It uses localstorage as server to load and save informtions.
                            You can login, and choose level difficulty. To play this game, you need to go admininistrator section to load all
                            questions and answers an than go back to main page to login. Desktop version only.
                         </div>
                        <div className="images">

                            <div className="image"><img src={img1} alt="" /></div>
                        </div>
                        <div className="images-full">
                            <div className="image"><img src={img2} alt="" /></div>
                            <div className="image"><img src={img3} alt="" /></div>
                            <div className="image"><img src={img4} alt="" /></div>
                        </div>
                    </div>
                </div>
                <Footer />
            </div>
        </div>
    )
}

export default QuizNow
