import React from 'react'
import vadoo from './../../img/31.PNG';
import fanatico from './../../img/fanatico-3.PNG';
import dev from './../../img/000dev-1.PNG';
import quiznow from './../../img/quiznow-1.PNG';
import { Link } from 'react-router-dom';

const MyProjects = () => {
    return (
        <div className="my-projects" id="my-projects">
            <div className="container">
                <h1>My Projects</h1>
                <div className="projects">
                    <Link to="/vadoo" className="project">
                        <div className="overlay"></div>
                        <img src={vadoo} alt="" />
                        <div className="name">Vadoo App</div>
                    </Link>
                    <Link to="/fanatico" className="project">
                        <div className="overlay"></div>
                        <img src={fanatico} alt="" />
                        <div className="name">Fanatico</div>
                    </Link>
                    <Link to="/000dev" className="project">
                        <div className="overlay"></div>
                        <img src={dev} alt="" />
                        <div className="name">000dev</div>
                    </Link>
                    <Link to="/quiznow" className="project">
                        <div className="overlay"></div>
                        <img src={quiznow} alt="" />
                        <div className="name">QuizNow</div>
                    </Link>
                </div>
            </div>
        </div>
    )
}

export default MyProjects
