import React from 'react'
import me from './../../img/me.jpg';
import Typed from 'react-typed';
const AboutMe = () => {
    return (
        <div className="about-me-page" id="about-me">
            <div className="container">
                <h1>About me</h1>
                <div className="about-me-page-desc">
                    <div className="left">
                        <div className="image">
                            <img src={me} alt="" />
                        </div>
                    </div>
                    <div className="right">
                        <h2>
                            I'm Faruk Obradović and <span><Typed
                                strings={['Web Developer', 'Student']}
                                typeSpeed={60}
                                backSpeed={60}
                                loop
                            /></span>
                        </h2>
                        <p>
                            Hi! My name is Faruk Obradović. I am a Web Developer, and I'm
                            very passionate and dedicated to my work. My skills are HTML, CSS, JS, SASS, React JS and C++
                            . I'm studyng FIT, and Im final 3rd year. I enjoy every step of the design process, from
                            discussion and collaboration to concept and execution, but I
                            find the most satisfaction in seeing the finished product do
                            everything for you that it was created to do.
                </p>
                        <div className="right-infos">
                            <div className="right-infos-left">
                                <p><span>Birthday: </span> 24.04.1995</p>
                                <p><span>City: </span> Mostar</p>
                                <p><span>Study: </span>FIT Mostar</p>
                            </div>
                            <div className="right-infos-right">
                                <p><span>Age: </span>24</p>
                                <p><span>Degree: </span>Student</p>
                                <p><span>Mail: </span>faruk_95@msn.com</p>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    )
}

export default AboutMe
