import React, { useEffect } from 'react'
import Navbar from './Navbar'
import Footer from './Footer'
import img1 from './../../img/vadoo-1.png';
import img2 from './../../img/vadoo-4.png';
import img3 from './../../img/31.PNG';
import img4 from './../../img/32.PNG';
import img5 from './../../img/33.PNG';
import imgGif from './../../img/VadooGif.gif';
import { Helmet } from "react-helmet";

const Vadoo = () => {
    useEffect(() => {
        window.scrollTo(0, 0)
    }, [])
    return (
        <div className="container-portfolio">
            <Helmet>
                <meta charSet="utf-8" />
                <title>Vadoo</title>
            </Helmet>
            <Navbar />
            <div className="pages">
                <div className="container">
                    <div className="project-info">
                        <h1>Vadoo App</h1>
                        <div className="buttons">
                            <a
                                href="https://github.com/farukobradovic/vadooapp"
                                className="button-github">Github</a>
                        </div>
                        <div className="desc">
                            Vadoo app is inspired by Tinder and Badoo. This app uses json server, is created by using react and SASS.
                            Mobile and desktop version included. I didn't put this in deploy because it isn't fully functional without json server, which i used in my local machine.
                            User can register, login, logout, find matches, input parametres for search etc.
                            </div>
                        <div className="images">
                            <div className="image"><img src={img1} alt="" /></div>
                            <div className="image"><img src={img2} alt="" /></div>
                        </div>
                        <div className="images-full">
                            <div className="image"><img src={img3} alt="" /></div>
                            <div className="image"><img src={img4} alt="" /></div>
                            <div className="image"><img src={img5} alt="" /></div>
                            <div className="image"><img src={imgGif} alt="" /></div>
                        </div>
                    </div>
                </div>
                <Footer />
            </div>
        </div>
    )
}

export default Vadoo
