import React, { useEffect } from 'react'
import Navbar from './Navbar'
import Footer from './Footer'
import img1 from './../../img/fanatico-1 (2).png';
import img2 from './../../img/fanatico-2.png';
import img3 from './../../img/fanatico-3.PNG';
import img4 from './../../img/fanatico-4.PNG';
import img5 from './../../img/fanatico-5.PNG';
import { Helmet } from "react-helmet";

const Fanatico = () => {
    useEffect(() => {
        window.scrollTo(0, 0)
    }, [])
    return (
        <div className="container-portfolio">
            <Helmet>
                <meta charSet="utf-8" />
                <title> Fanatico</title>

            </Helmet>
            <Navbar />
            <div className="pages">
                <div className="container">
                    <div className="project-info">
                        <h1>Fanatico</h1>
                        <div className="buttons">
                            <a
                                href="https://farukobradovic.github.io/fanaticoapp/#/"
                                className="button-preview">Live Preview</a>
                            <a
                                href="https://github.com/farukobradovic/fanaticoapp"
                                className="button-github">Github</a>
                        </div>
                        <div className="desc">
                            Fanatico is app created by 2 APIs in React. I used SASS also. One API generates sports news, ans second football statistics about every league, club or player.
                             </div>
                        <div className="images">
                            <div className="image"><img src={img1} alt="" /></div>
                            <div className="image"><img src={img2} alt="" /></div>
                        </div>
                        <div className="images-full">
                            <div className="image"><img src={img3} alt="" /></div>
                            <div className="image"><img src={img4} alt="" /></div>
                            <div className="image"><img src={img5} alt="" /></div>
                        </div>
                    </div>
                </div>
                <Footer />
            </div>
        </div>
    )
}

export default Fanatico
