import React, { useEffect } from 'react'
import Navbar from './Navbar'
import Footer from './Footer'
import img1 from './../../img/000dev-2.png';
import img2 from './../../img/000dev-5.png';
import img3 from './../../img/000dev-1.PNG';
import img4 from './../../img/000dev-4.PNG';
import img5 from './../../img/000dev-3.PNG';
import { Helmet } from "react-helmet";

const Dev = () => {
    useEffect(() => {
        window.scrollTo(0, 0)
    }, [])

    return (
        <div className="container-portfolio">
            <Helmet>
                <meta charSet="utf-8" />
                <title>000dev</title>
            </Helmet>
            <Navbar />
            <div className="pages">
                <div className="container">
                    <div className="project-info">
                        <h1>000dev</h1>
                        <div className="buttons">
                            <a
                                href="https://000dev.netlify.com/"
                                className="button-preview">Live Preview</a>
                            <a
                                href="https://github.com/farukobradovic/000dev-Startup"
                                className="button-github">Github</a>
                        </div>
                        <div className="desc">
                            This web page is created using HTML, CSS and SASS. It's static, nothing complicated. Mobile and desktop version included.
                            </div>
                        <div className="images">
                            <div className="image"><img src={img1} alt="" /></div>
                            <div className="image"><img src={img2} alt="" /></div>
                        </div>
                        <div className="images-full">
                            <div className="image"><img src={img3} alt="" /></div>
                            <div className="image"><img src={img4} alt="" /></div>
                            <div className="image"><img src={img5} alt="" /></div>
                        </div>
                    </div>
                </div>
                <Footer />
            </div>
        </div>
    )
}

export default Dev
