import React from 'react'
// import { Link } from 'react-router-dom';
import { HashLink as Link } from 'react-router-hash-link'

const NavbarLinks = () => {
    const onClick = () => {
        window.scrollTo(0, 0);
    }
    return (
        <div className="desc">
            <ul className="list">
                <li className="list-item">
                    <Link to='/' className="list-item-link" onClick={onClick}>Home</Link>
                </li>
                <li className="list-item">
                    <Link to="/#about-me" className="list-item-link">About</Link>
                </li>
                <li className="list-item">
                    <Link to="/#my-projects" className="list-item-link">My Projects</Link>
                </li>
            </ul>
            <div className="social-links">
                <a href="https://github.com/farukobradovic" className="social-link"
                ><i className="fab fa-github"></i
                ></a>
                <a href="#" className="social-link"
                ><i className="fab fa-facebook-f"></i
                ></a>
            </div>
        </div>
    )
}

export default NavbarLinks
