import React from 'react'
import bg from './../../img/123.jpg';
import me from './../../img/3.jpg';
import Typed from 'react-typed';

const MainPage = () => {
    return (
        <div className="main-page">
            <div className="background-image">
                <img src={bg} alt="" />
            </div>
            <div className="absolute-info">
                <div className="image">
                    <img src={me} alt="" />
                </div>
                <div className="desc">
                    <h1>Faruk <span>Obradović</span></h1>
                    {/* <p>I'm <span>Web Developer / Student</span></p> */}
                    <p>I'm <span><Typed
                        strings={['Web Developer', 'Student']}
                        typeSpeed={60}
                        backSpeed={60}
                        loop
                    /></span></p>


                </div>
            </div>
        </div>
    )
}

export default MainPage
