import React from 'react';
import './App.css';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';
import Home from './components/pages/Home';
import Vadoo from './components/pages/Vadoo';
import Fanatico from './components/pages/Fanatico';
import Dev from './components/pages/Dev';
import QuizNow from './components/pages/QuizNow';

function App() {
  return (
    <Router basename="/">
      <div className="App">
        <Switch>
          <Route exact path='/' component={Home} />
          <Route exact path='/vadoo' component={Vadoo} />
          <Route exact path='/fanatico' component={Fanatico} />
          <Route exact path='/000dev' component={Dev} />
          <Route exact path='/quiznow' component={QuizNow} />
        </Switch>
      </div>
    </Router>
  );
}

export default App;
